:root {
  --colour-black: #000000;
  --colour-white: #ffffff;
}

:root {
  --spacing-tiny: 0.5rem;
  --spacing-small: 1rem;
  --spacing-medium: 2rem;
  --spacing-large: 4rem;
  --spacing-huge: 8rem;
  --spacing-gigantic: 16rem;
}

:root {
  --font-size-ratio: 1.25;
  --font-size-tiny: 1rem;
  --font-size-small: calc(var(--font-size-tiny) * var(--font-size-ratio));
  --font-size-medium: calc(var(--font-size-small) * var(--font-size-ratio));
  --font-size-large: calc(var(--font-size-medium) * var(--font-size-ratio));
  --font-size-huge: calc(var(--font-size-large) * var(--font-size-ratio));
  --font-size-gigantic: calc(var(--font-size-huge) * var(--font-size-ratio));
  --font-family-sans-serif: sans-serif;
  --font-family-serif: serif;
  --font-weight-regular: 400;
  --font-weight-bold: 700;
  --line-height-tiny: 1.6;
  --line-height-small: 1.5;
  --line-height-medium: 1.4;
  --line-height-large: 1.3;
  --line-height-huge: 1.2;
  --line-height-gigantic: 1.1;
}
@media (min-width: 768px) {
  :root {
    --font-size-ratio: 1.333;
  }
}
@media (min-width: 1024px) {
  :root {
    --font-size-ratio: 1.414;
  }
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

.o-layout {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-medium);
}

.o-layout--flush {
  gap: 0;
}

.o-layout--small {
  gap: var(--spacing-small);
}

.o-layout--medium {
  gap: var(--spacing-medium);
}

.o-layout--large {
  gap: var(--spacing-large);
}

.o-layout--direction-row {
  flex-direction: row;
}

.o-layout--direction-row-reverse {
  flex-direction: row-reverse;
}

.o-layout--direction-column {
  flex-direction: column;
}

.o-layout--direction-column-reverse {
  flex-direction: column-reverse;
}

.o-layout--align-start {
  align-items: start;
}

.o-layout--align-end {
  align-items: end;
}

.o-layout--align-center {
  align-items: center;
}

.o-layout--align-stretch {
  align-items: stretch;
}

.o-layout--align-baseline {
  align-items: baseline;
}

.o-layout--justify-start {
  justify-content: start;
}

.o-layout--justify-end {
  justify-content: end;
}

.o-layout--justify-center {
  justify-content: center;
}

.o-layout--justify-space-between {
  justify-content: space-between;
}

.o-layout--justify-space-around {
  justify-content: space-around;
}

.o-layout--justify-space-evenly {
  justify-content: space-evenly;
}

